import { gsap } from "gsap";

export default class {
    constructor (container) {
        this.container = container;
        this.els = [];
        this.tl = gsap.timeline();
        this.timer = 0;

        this.init()
    }

    init () {
        this.addElements()
    }


    // sur chaque section foreach des lments 
    addElements () {
        this.els = this.container.querySelectorAll('[data-parallaxe]');
        const options = {
            root: null,
            rootMargin: "0px"
        };
        this.observer = new IntersectionObserver(cb => this.isInVew.call(this, cb), options);

        this.els.forEach(el => {
            this.observer.observe(el);
        })
    }

    // Chck if element is inview
    // detectElement () {
    //     const els = this.sections[y].el.querySelectorAll(`[data-${this.name}]`);
    // }

    // intersection observer zone (section et els)
    isInVew (entries) {
        // rcuprer la target pour les lments visibles
        console.log(entries)
        const elements = entries.filter(entry => (entry.isIntersecting === true)).map(entry => entry.target);
        elements.forEach(entry => this.observer.unobserve(entry));

        if(elements.length > 0) {
            // metter un forEach de l'array et changer le timer ddedans
            elements.forEach(element => {
                // gsap.to(element, {autoAlpha: 1, duration: 1, delay: this.timer, ease: "Power3.easeInOut", onComplete: _ => this.timer -= .2});
                if(element.dataset.parallaxe === "img") {
                    gsap.to(element, {clipPath: "polygon(0 0%, 100% 0%, 100% 100%, 0% 100%)", duration: 1, delay: this.timer, ease: "Power3.easeInOut", onComplete: _ => this.timer -= .2});
                } else {
                    gsap.to(element, {autoAlpha: 1, duration: 1, delay: this.timer, ease: "Power3.easeInOut", onComplete: _ => this.timer -= .2});
                }

                this.timer = this.timer + .2;

            })
        }
    }
 
    destroy () {
        this.els.forEach(el => this.observer.unobserve(el));
        this.observer.disconnect();
        this.tl.kill();

        for (let prop in this) {
            if (!Object.prototype.hasOwnProperty.call(this, prop)) continue;
  
            this[prop] = null;
            delete this[prop];
        }
    }
}